import axios from 'axios'
axios.defaults.withCredentials = true;


function ConnectionHandlerPost(path,params,firstCallback=null,finalCallback=null){
    console.log('test');
    axios.post(path,
        params
    ).then(response =>{
        if(response.status == 200){
            firstCallback(response);
        }
    }).catch(error=>{
        console.log(error.response.status)
    }).finally(() =>{
        if(finalCallback != null){
            finalCallback();
        }
    });   
}  

function ConnectionHandlerGet (path,params,firstCallback=null,finalCallback=null){
    axios.get(path,{
        params:params
    }).then(response =>{
        if(response.status == 200){
            console.log(response.data);
            if(firstCallback != null){
                firstCallback(response.data);
            }
        }
    }).catch(error=>{
        console.log(error.response.status)
    }).finally(()=>{
        console.log("FINALLY GET")
    })
}

export default {ConnectionHandlerGet,ConnectionHandlerPost}