<template>
    <div class='configuration__form'>
        <router-view name='sectionOne'></router-view>
        <router-view name='sectionTwo'></router-view>
        <router-view name='sectionThree'></router-view>
        <router-view name='sectionFour'></router-view>
        <input type='button' value='LAUNCH' class='form__submit__btn' v-on:click='launchConfiguration()'>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputNumber from '@/components/Input_Number';
import ConnectionHandler from '@/javascript/connectionHandler'
export default {
    components:{
        'input-select':InputSelect,
        'input-number':InputNumber,
    },

    methods:{
        launchConfiguration:function(){

            let params = {"game_config": {
"duration" : {"value": 1, "type" : "years"},
"human_agent": {"amount" : 1},
"habitat" : "crew_habitat_small",
"greenhouse" : "greenhouse_large",
"food_storage":{"amount":1000},
"plants" : [{"species" : "spinach", "amount": 10}, {"species": "cabbage", "amount" : 10}],
"solar_arrays" : {"amount":50},
"power_storage" : {"amount":160} }}
            ConnectionHandler.ConnectionHandlerPost('/new_game',params,(response)=> {
                console.log(response);
                ConnectionHandler.ConnectionHandlerGet('/get_step',{'step_num':1})})    
        }
    }
}
</script>

<style lang="scss" scoped>
    .configuration__form{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .form__section__title{
        width: fit-content;
        font-size: 1,5rem;
        color: lightgreen;
        font-family:'nasalization';
        margin-bottom: 32px;
        box-sizing: border-box;

        &:after{
            content:'';
            display:block;
            margin: 4px 0px;
            border-bottom: 1px solid grey;
        }
    }

    .input__wrapper{
        margin-bottom: 16px;
    }

    .input__wrapper--flex{
        display: flex;
        justify-content:flex-start;
        align-items:center;
    }

    .form__submit__btn{
        margin-top:auto;
        padding: 8px 16px;
        border: 2px solid #bb3b3b;
        border-radius:10px;
        width: 60%;
        box-sizing: border-box;
        background-color: transparent;
        color: white;
        font-size: 1.5rem;
        font-weight: 900;
        font-family:'nasalization';
        transition:ease-in-out .15s;
    }

    .form__submit__btn:hover{
        background-color: #ea3b3b;
        border-color: #ea3b3b;
    }

    .form__submit__btn{

        align-self:center;
    }

</style>
